@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

img {
  width: 100%;
  display: inline-block;
}

body {
  font-family: 'PT Sans Narrow', sans-serif;
  font-weight: 400;
  box-sizing: border-box;
}

.header {
  box-shadow: 0 2px 9px rgba(189, 189, 189, 0.5);
  padding: 20px 0 19px;
  font-size: 0;
  overflow: hidden;
  position: fixed;
  width: 100%;
  z-index: 10000;
}

.container {
  max-width: 1365px;
  margin: 0 auto;
}

.container:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 1410px) {
  .container {
    margin: 0 20px;
  }
}

.header-top:after {
  content: " ";
  display: table;
  clear: both;
}

.header-top__left {
  float: left;
}

.header-top__right {
  float: right;
}

.logo {
  display: inline-block;
  vertical-align: middle;
}

.nav {
  display: inline-block;
  margin-left: 69px;
  vertical-align: middle;
}

@media only screen and (max-width: 750px) {
  .nav {
    display: none;
  }
}

.nav__item {
  display: inline-block;
  position: relative;
  overflow: hidden;
  margin-right: 57px;
}

.nav__item:hover::before, .nav__item.is-active::before {
  transform: translate(0, 0);
}

.nav__item::before {
  position: absolute;
  left: 0;
  width: 100%;
  bottom: 0;
  height: 1px;
  background: #333743;
  content: '';
  transform: translate(-100%, 0);
  transition: transform .2s linear;
}

.nav__link {
  text-decoration: none;
  color: #9297a3;
  text-transform: uppercase;
  font-size: 11px;
}

.nav .is-active {
  color: #333743;
}

.products-item {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  top: 8px;
  margin-right: 65px;
}

.products-item__name {
  color: #333743;
  font-size: 13px;
  margin-right: 13px;
  text-decoration: none;
}

.products-item__number {
  color: #ffffff;
  font-family: Georgia, sans-serif;
  font-size: 11px;
  font-weight: 400;
  background-color: #333743;
  height: 15px;
  width: 15px;
  border-radius: 50%;
  text-align: center;
  display: inline-block;
  text-decoration: none;
}

.account {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  top: 8px;
  font-size: 13px;
  text-decoration: none;
}

.account::before {
  content: '';
  height: 70px;
  top: -29px;
  left: -36px;
  width: 1px;
  background: #dedede;
  position: absolute;
}

.account__login {
  margin-right: 17px;
  text-decoration: none;
  color: #333743;
}

.account__sing-upe {
  margin-left: 16px;
  text-decoration: none;
  opacity: 0.7;
  cursor: pointer;
  color: #333743;
}

.header-bottom {
  margin-top: 20px;
  display: none;
}

@media only screen and (max-width: 750px) {
  .header-bottom {
    display: block;
  }
}

.menu-mobile {
  position: relative;
}

.hamburger {
  display: none;
  position: absolute;
  left: 0;
  width: 20px;
  height: 20px;
  cursor: pointer;
}

@media only screen and (max-width: 750px) {
  .hamburger {
    display: block;
  }
}

.hamburger span {
  display: block;
  width: 20px;
  height: 2px;
  position: relative;
  background: #333743;
}

.hamburger span:before {
  content: '';
  display: block;
  width: 20px;
  height: 2px;
  position: absolute;
  top: 5px;
  background: #333743;
}

.hamburger span:after {
  content: '';
  display: block;
  width: 20px;
  height: 2px;
  position: absolute;
  top: -5px;
  background: #333743;
}

.cross {
  position: absolute;
  right: 0;
  width: 20px;
  height: 2px;
  display: block;
  background: #333743;
}

@media only screen and (max-width: 750px) {
  .cross {
    display: none;
  }
}

.cross:nth-of-type(1) {
  transform: rotate(45deg);
}

.cross:nth-of-type(2) {
  transform: rotate(-45deg);
}

