label {
  display: inline-block;
  cursor: pointer;
  position: relative;
  padding-left: 25px;
  font-size: 13px;
  font-weight: 400;
  font-family: Georgia, sans-serif;
  color: #9297a3;
}

input[type=radio] {
  display: none;
}

label:before {
  content: '';
  display: block;
  border: 1px solid #9297a3;
  width: 10px;
  height: 10px;
  background-color: #ffffff;
  position: absolute;
  left: -22px;
  top: 50%;
  transform: translate(0, -50%);
}

label:before {
  border-radius: 8px;
}

input[type=radio]:checked + label:before {
  content: url("../img/check.png");
  text-align: center;
  line-height: 8px;
  background-color: #333743;
}

